@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', 'sans-serif';
}

header {
  background-image: url('./images/cover.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 150px;
  position: relative;
}

.overlay {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  right: 0;
  bottom: 0;
  padding: 0 !important;
  margin: 0 !important;
  background-color: rgba(10, 10, 10, 0.3);
}

nav {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 50px 60px 0 60px;
}

nav li {
  list-style: none;
  display: inline-block;
  padding-right: 10px;
  padding-left: 10px;
}

li a {
  padding: 5px;
  background-color: white;
  text-decoration: none;
}

.cart ion-icon {
  vertical-align: bottom;
  font-size: 20px;
  padding-right: 5px;
}
 .cart a {
   background-color: royalblue;
   color: white;
   cursor: pointer;
   text-decoration: none;
 }

 .container,
 .container-products {
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
   margin-top: 50px;
   padding-bottom: 100px;
 }

 .imagleFile {
   width:200px;
   height: 200px;
 }

 .image {
   margin-right: 20px;
   margin-left: 20px;
   position: relative;
   overflow: hidden;
 }

 .addToCart {
   position: absolute;
   width: 100%;
   background-color: darkgray;
   transition: all 0.3s ease-in-out;
   opacity: 0;
   cursor: pointer;
   text-align: center;
 }

 .image:hover .cart1,
 .image:hover .cart2,
 .image:hover .cart3,
 .image:hover .cart4 {
  bottom: 50px;
  opacity: 1;
  padding: 10px;
  text-decoration: none;
 }

 /* The styles of our cart page */

 .container-products {
   max-width: 650px;
   justify-content: space-around;
   margin: 0 auto;
   margin-top: 50px;
 }

 .container-products ion-icon {
   font-size: 25px;
   color: blue;
   margin-left: 10px;
   margin-right: 10px;
   cursor: pointer;
 }

 .product-header {
   width: 100%;
   max-width: 650px;
   display: flex;
   justify-content: flex-start;
   border-bottom: 4px solid lightgray;
   margin: 0 auto;
 }

 .product-title {
   width: 45%;
 }

 .price {
   width: 15%;
   border-bottom: 1px solid lightgray;
   align-items: center;
 }

 .quantity {
   width: 30%;
   border-bottom: 1px solid lightgray;
   display: flex;
   align-items: center;
 }

 .total {
   width: 10%;
   border-bottom: 1px solid lightgray;
   display: flex;
   align-items: center;
 }

 .product {
   width: 45%;
   display: flex;
   justify-content: space-around;
   align-items: center;
   padding: 10px 0;
   border-bottom: 1px solid lightgray;
 }

 .product ion-icon {
   cursor: pointer;
 }

 .products {
   width: 100%;
   display: flex;
   flex-wrap: wrap;
 }

 .products img {
   width: 75px;
 }

 .basketTotalContainer {
   display: flex;
   justify-content: flex-end;
   width: 100%;
   padding: 10px 0;
 }

 .basketTotalTitle {
   width: 30%;
 }

 .basketTotal {
   width: 10%
 }

 @media(max-width:"500px") {
   nav h2 {
     width: 100%;
     text-align: center;
     margin-bottom: 10px;
   }
   .sm-hide {
     display: none;
   }
   .products,
   .product-header {
     justify-content: center;
   }
   .basketTotalContainer {
     padding-right: 8%;
   }
 }

 .backToShopping {
  padding-right: 180px;
  color: royalblue;
  padding-top: 5px;
 }

 a {
   text-decoration: none;
 }

 h5.quantity {
  padding-left: 15px;
 }

 a.button3 {
  display:inline-block;
  padding:0.3em 1.2em;
  margin:0 0.3em 0.3em 0;
  border-radius:2em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:#FFFFFF;
  background-color: green;
  text-align:center;
  transition: all 0.2s;
 }

 a.button3:hover {
   background-color: #4095c6;
 }

 @media all and (max-width:30em)
 
 { a.button3
  { display:block;
   margin:0.2em auto; } 
  }